export default function (context) {
	const fetch = require('sketch-polyfill-fetch');
	
	const url = context.plugin.urlForResourceNamed("params.json");
	let result = NSString.stringWithContentsOfFile_encoding_error(url, NSUTF8StringEncoding, null);
	let params = JSON.parse(result);
	log(params);
	let colors = JSON.stringify(params.colorPalete).slice(1).slice(0,-1);
	let progressColor = JSON.stringify(params.progressParams.progressBarColor);
	let barWidth = params.barWidthParam;
	let sparkColor = JSON.stringify(params.sparkParams.sparkColor);
	let curveType = "";
	let scatterType = "";
	let dotsState = "";
	let cutState = "";
	if (JSON.stringify(params.curveType) == "1") {
		curveType = "Yes";
	} else {
		curveType = "No";
	}
	if (params.lineParams.dots === "true") {
		dotsState = "Yes";
	} else {
		dotsState = "No";
	}
	if (params.lineParams.cuttedCenter === "true") {
		cutState = "Yes";
	} else {
		cutState = "No";
	}
	if (params.scatterParams.bubbles === "true") {
		scatterType = "Yes";
	} else {
		scatterType = "No";
	}

	let curveView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))
	let curveSelect = NSPopUpButton.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
    	curveSelect.addItemsWithTitles(["Yes","No"])
    	curveSelect.selectItemWithTitle(curveType)
	    curveView.addSubview(curveSelect)

	let scatterView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))
	let scatterSelect = NSPopUpButton.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
    	scatterSelect.addItemsWithTitles(["Yes","No"])
    	scatterSelect.selectItemWithTitle(scatterType)
	    scatterView.addSubview(scatterSelect)


	let colorsView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 80.0))

    let colorsInput = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 80.0))
	    colorsInput.cell().setPlaceholderString("Ð¡olors separated by commas in format: [R,G,B]")
	    colorsInput.cell().setStringValue("" + colors)
	    colorsView.addSubview(colorsInput)


	let linesSecondView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))

	let dotSelect = NSPopUpButton.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
    	dotSelect.addItemsWithTitles(["Yes","No"])
    	dotSelect.selectItemWithTitle(dotsState)
	    linesSecondView.addSubview(dotSelect)

	let cutSelect = NSPopUpButton.alloc().initWithFrame(NSMakeRect(140.0, 0.0, 120.0, 25.0))
    	cutSelect.addItemsWithTitles(["Yes","No"])
    	cutSelect.selectItemWithTitle(cutState)
	    linesSecondView.addSubview(cutSelect)


	let progressView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))

	let progressColorInput = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
		progressColorInput.cell().setPlaceholderString("e.g., [52,52,52]")
		progressColorInput.cell().setStringValue("" + progressColor)
		progressView.addSubview(progressColorInput)

	let barsView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))

	let barsInput = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
		barsInput.cell().setPlaceholderString("from 0.1 to 0.9")
		barsInput.cell().setStringValue("" + barWidth)
		barsView.addSubview(barsInput)

	let sparkView = NSView.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 260.0, 25.0))

	let sparkInput = NSTextField.alloc().initWithFrame(NSMakeRect(0.0, 0.0, 120.0, 25.0))
		sparkInput.cell().setPlaceholderString("e.g., [52,52,52]")
		sparkInput.cell().setStringValue("" + sparkColor)
		sparkView.addSubview(sparkInput)



    let alert = COSAlertWindow.new();
	    alert.setMessageText("Parameters for Chart")
	    alert.addTextLabelWithValue("Smoothed lines")
	    alert.addAccessoryView(curveView)
	    alert.addTextLabelWithValue("Colors")
	    alert.addAccessoryView(colorsView)
	    alert.addTextLabelWithValue("Line chart: Dots & Cutted center")
	    alert.addAccessoryView(linesSecondView)
	    alert.addTextLabelWithValue("Width of bars")
	    alert.addAccessoryView(barsView)
	    alert.addTextLabelWithValue("Progress bar: Color")
	    alert.addAccessoryView(progressView)
	    alert.addTextLabelWithValue("Sparkline: Color")
	    alert.addAccessoryView(sparkView)
	    alert.addTextLabelWithValue("Scatter Plot: Bubbles")
	    alert.addAccessoryView(scatterView)
	    alert.addButtonWithTitle("OK")
	    alert.addButtonWithTitle("Cancel")

	alert.alert().window();

	let responseCode = alert.runModal();


	let newColors = colorsInput.stringValue(),
		newCurve = curveSelect.indexOfSelectedItem(),
		newLineDot = dotSelect.indexOfSelectedItem(),
		newLineCut = cutSelect.indexOfSelectedItem(),
		newProgressColor = progressColorInput.stringValue(),
		newBarsWidth = barsInput.stringValue(),
		newSparkColor = sparkInput.stringValue(),
		newBubble = scatterSelect.indexOfSelectedItem();

	let newColorsForm = JSON.parse("[" + newColors + "]")

	params.colorPalete = newColorsForm;
	let newLineDotForm = "true",
		newLineCutForm = "true",
		newBubbleForm = "true";

	if (newCurve == "0") {
		newCurve = 1;
	} else {
		newCurve = 0;
	}
	if (newLineDot == "0") {
		newLineDotForm = "true";
	} else {
		newLineDotForm = "false";
	}
	if (newLineCut == "0") {
		newLineCutForm = "true";
	} else {
		newLineCutForm = "false";
	}
	if (newBubble == "0") {
		newBubbleForm = "true";
	} else {
		newBubbleForm = "false";
	}
	params.curveType = newCurve;
	params.lineParams.dots = newLineDotForm;
	params.scatterParams.bubbles = newBubbleForm;
	params.lineParams.cuttedCenter = newLineCutForm;
	params.barWidthParam = Number(newBarsWidth);
	params.progressParams.progressBarColor = JSON.parse(newProgressColor);
	params.sparkParams.sparkColor = JSON.parse(newSparkColor);

	log(params);

	let contentString = NSString.stringWithString(JSON.stringify(params));

	contentString.writeToFile_atomically_encoding_error(url, true, NSUTF8StringEncoding, null);


}